import React from "react";

const style = {
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  padding: ".5rem 1rem",
  border: "1px solid #ccc",
  borderRadius: "4px",
  marginBottom: ".5rem",
};

function NotesPreview({ notesId, title, onClick }) {
  return (
    <li style={style} onClick={() => onClick((notesId = { notesId }))}>
      <span>
        <strong>{notesId}</strong>
        &nbsp;
        {title}
      </span>
    </li>
  );
}

export default NotesPreview;
